import { isDate, isObject } from '../helpers/util'
import { parseParamsByRules } from './rules'

export function createStorage(areaName, options) {
  if (!isObject(options)) {
    options = {}
  }

  let { areaName: prefix, perLimitSize, limitSize } = parseParamsByRules(
    Object.assign({ areaName }, options),
    'createStorage'
  )

  prefix += ':'

  class StorageArea {
    /**
     *  key 
     * @param key
     */
    getStorage(key) {
      parseParamsByRules(
        {
          key
        },
        'getStorage'
      )

      const value = localStorage.getItem(prefix + key)

      if (value) {
        try {
          let ret = JSON.parse(value)

          if (ret.isApp) {
            return ret.isDate ? new Date(ret.data) : ret.data
          }
        } catch (e) {
          //
        }

        return null
      }
    }
    /**
     *  key  key  key  1024KB 2560KB
     * @param key
     * @param data
     */
    setStorage(key, data) {
      parseParamsByRules(
        {
          key,
          data
        },
        'setStorage'
      )

      const storageInfo = this.getStorageInfo()

      const value = JSON.stringify({
        isDate: isDate(data),
        data
      })

      const valueSize = value.length / 1024

      if (valueSize > perLimitSize) {
        throw new Error(
          `Storage error: per storage limit size exceeded ${perLimitSize}KB.`
        )
      }

      if (storageInfo.currentSize + valueSize > storageInfo.limitSize) {
        // 
        throw new Error(
          `Storage error: total storage limit size exceeded ${limitSize}KB.`
        )
      }

      localStorage.setItem(prefix + key, value)
    }
    /**
     *  key
     * @param key
     */
    removeStorage(key) {
      parseParamsByRules(
        {
          key
        },
        'removeStorage'
      )

      localStorage.removeItem(prefix + key)
    }
    /**
     * 
     */
    clearStorage() {
      // localStorage.clear()

      for (let i = 0; i < localStorage.length; i++) {
        const key = localStorage.key(i)

        if (key.indexOf(prefix) === 0) {
          // api
          localStorage.removeItem(key)
        }
      }
    }
    /**
     * storage
     */
    getStorageInfo() {
      const keys = []
      let size = 0

      for (let i = 0; i < localStorage.length; i++) {
        const key = localStorage.key(i)

        if (key.indexOf(prefix) === 0) {
          // api
          keys.push(key.replace(prefix, ''))
        }

        size += localStorage.getItem(key).length
      }

      return {
        keys,
        currentSize: size / 1024,
        limitSize: limitSize
      }
    }
  }

  return new StorageArea()
}
